@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
* {
  box-sizing: border-box;
}

body,
h1,
h2,
p {
  margin: 0;
}

input,
button {
  font: inherit;
  display: block;
  border: none;
  background: none;
}

button {
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

a {
  color: #000;
}

.flow-content > * + * {
  margin-top: 2rem;
  margin-top: var(--space, 2rem);
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  background: #4568dc; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #b06ab3,
    #4568dc
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
}

.flex-container {
  display: -webkit-flex;
  display: flex;
}

.nav-container {
  padding: 1rem 1.25rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.25rem;
  font-weight: 500;
}

.nav-container .wrapper {
  width: 5rem;
  height: 5rem;
  padding: 0.25rem;
  background: #4568dc; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #b06ab3, #4568dc);
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.25);
}

.nav-container .wrapper img {
  margin: auto;
}

.links a + a {
  margin-left: 1rem;
}

.input-container p {
  text-align: center;
}

.inputs {
  max-width: 40rem;
  background: radial-gradient(
        circle farthest-side at 0% 50%,
        #fb1 23.5%,
        rgba(240, 166, 17, 0) 0
      )
      21px 30px,
    radial-gradient(
        circle farthest-side at 0% 50%,
        #b71 24%,
        rgba(240, 166, 17, 0) 0
      )
      19px 30px,
    linear-gradient(
        #fb1 14%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 85%,
        #fb1 0
      )
      0 0,
    linear-gradient(
        150deg,
        #fb1 24%,
        #b71 0,
        #b71 26%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 74%,
        #b71 0,
        #b71 76%,
        #fb1 0
      )
      0 0,
    linear-gradient(
        30deg,
        #fb1 24%,
        #b71 0,
        #b71 26%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 74%,
        #b71 0,
        #b71 76%,
        #fb1 0
      )
      0 0,
    linear-gradient(90deg, #b71 2%, #fb1 0, #fb1 98%, #b71 0%) 0 0 #fb1;
  background-size: 40px 60px;
  padding: 1.5rem;
  margin: 2rem auto 0;
  border-radius: 5px;
}

.inputs input {
  -webkit-flex-basis: 65%;
          flex-basis: 65%;
  background: #fff;
  border: 2px solid #000;
  border-radius: 5px;
  padding: 0.25rem;
}

.inputs input::-webkit-input-placeholder {
  opacity: 0.45;
}

.inputs input:-ms-input-placeholder {
  opacity: 0.45;
}

.inputs input::placeholder {
  opacity: 0.45;
}

.inputs .btn {
  font-size: 1rem;
  -webkit-flex-basis: 35%;
          flex-basis: 35%;
}

@media (max-width: 650px) {
  .inputs {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .inputs button {
    margin: 0.5rem 0 0 0;
  }
}

.title {
  text-align: center;
  color: #ffffff;
  font-size: 2.75rem;
}

span {
  --space: 0.5rem;
  font-size: 4.25rem;
  font-weight: 700;
  display: block;
  color: #3b14a7;
  text-align: center;
}

.image-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.image-container .image {
  position: relative;
}

.image-container .inner-box {
  position: absolute;
  border: 2px solid #1768ac;
}

.form-wrapper {
  /* height: 100vh; */
  /* outline: 3px solid; */
  padding: 5rem 0;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.form-container {
  padding: 2rem;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
}

.form-title {
  text-align: center;
  font-weight: 500;
  font-size: 2.75rem;
  color: #fdfaf6;
}

.form-inputs {
  font-size: 1.35rem;
}

.form-inputs div {
  margin-bottom: 1.25rem;
}

.form-inputs label {
  font-weight: 400;
  font-size: 1rem;
  margin-bottom: 0.25rem;
}

.form-inputs input {
  font-size: 1.35rem;
  color: #fff;
  border: 2px solid #000;
  border-radius: 5px;
  padding: 0.25rem;
}

.form-inputs input:focus {
  background: #000;
}

.form-inputs .btn {
  font-size: 1.15rem;
  width: 100%;
  padding: 0.5rem;
  margin: 0 0 2rem 0;
}

.form-inputs a {
  display: block;
  text-align: center;
  font-size: 0.85rem;
  font-weight: 400;
}

.main-section {
  min-height: 86.35vh;
  padding: 2rem 1rem;
}

.particles {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.btn {
  font-weight: 500;
  background: #3b14a7;
  padding: 0.25rem 1rem;
  color: #fff;
  margin: 0 0 0 0.75rem;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.25s;
}

.btn:hover {
  background: #2b1075;
}

